.contact-prev{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 4rem;
    border-bottom: 1px solid white;
    
    img{
        width: 40px;
        height: 40px;
        margin-right: 20px;
    }

    .contact-info{
        display: flex;
        flex-direction: column;

        h5{
            color: #AD8350;
            font-weight: 700;
            font-size: 0.83rem;
        }
    }
}