/* * helpers */

.clean{
    margin:0;
    padding: 0;
}

.flat-btn{
    color: #000000;
    font-family: Montserrat;
    background-color: #764134;
}

.pad20{
    padding-left: 20px;
    padding-right: 20px;
}

.bottom-5{
    margin-bottom: 5px;
}

.bottom-10{
    margin-bottom: 10px;

}

.visible{
    visibility: visible;
}

.invisible{
    visibility: hidden;
}

.hidden {
    display: none;
}

.show {
    display: block;
}

.flex {
    display: flex;
}
.grow {
    flex-grow: 1;
}

.column {
    flex-direction: column;
}

.row {
    flex-direction: row;
}

.flex .full {
    flex: 1;
}

.justify-center {
    justify-content: center;
}

.space-around {
    justify-content: space-around;
}

.space-between {
    justify-content: space-between;
}

.align-center {
    align-items: center;
}

.flex.wrap {
    flex-wrap: wrap;
}
.align-self-center{
    align-self: center;
}

.justify-self-center{
    justify-content: center;
}

.flex-start{
    justify-content: flex-start;
}

.flex-end{
    justify-content: flex-end;
}